import {Link} from 'react-router-dom'
import PropTypes from 'prop-types'
import {useState} from 'react'

const Navbar = (props) => {

    const [title, setTitle] = useState(props.title);
    


    return (
        <div className="navbarWrapper">
            <div className="navbar">
                <div id="hamburger" onClick={()=>(toggleNav())} >
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
                <div id="pageTitle">
                    <h2>{title}</h2>
                </div>
            </div>
            <div className="navbarContent">
                <Link to='/settings' onClick={()=>(
                    setTitle('Settings'),
                    toggleNav()
                    )}>Settings</Link>
                <Link to='/game' onClick={()=>(
                    setTitle('Game'),
                    toggleNav()
                    )}>Game</Link>
                <Link to='/scoreboard' onClick={()=>(
                    setTitle('Scoreboard'),
                    toggleNav()
                    )}>Scoreboard</Link>
            </div>
        </div>

    )

    function toggleNav(){
        const navContent = document.querySelector('.navbarContent');
        navContent.classList.toggle('showNavigation');
    }
}





export default Navbar
