import PlayerPanel from './PlayerPanel'

const Game = (props) => {

    const players = props.players;

    return (
        <div className="view">
            <h2>Game</h2>
            <div className="playersPanel">
                {players.map(player => <PlayerPanel player={player} key={player.id}/>)}
            </div>
        </div>
    )
}

export default Game
